.rightbar{
    flex: 3.5;
    margin: 10px 0;
}
.rightbarWrapper{
padding: 20px 20px 0 0;
}
.birthdayContainer{
display: flex;
align-items: center;
}
.birthdayImage{
color:rgb(236, 52, 52);
margin-right: 10px;
}
.birthdayText{
font-size: 15px;
font-weight: 200
}
.righbarFriendList{
padding: 0;
margin: 0;
list-style: none;
}
.rightbarTitle{
    font-size: 18px;
font-weight: 500;
margin-bottom: 10px;
}
.rightbarInfo{
margin-bottom: 30px;
}
.rightbarInfoItem{
margin-bottom: 10px;
}
.rightbarInfoKey{
    font-weight: 500;
margin-right: 15px;
color: #555;
}
.rightbarInfoValue{
font-weight: 300;
}
.rightbarFollowings{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
}
.rightbarFollowing{
display: flex;
flex-direction: column;
margin-bottom: 20px;
cursor: pointer;
}
.rightbarFollowingImg{
width: 100px;
height: 100px;
object-fit: cover;
}